// Tab.scss

.tab_bar{
    position: fixed; left: 0; bottom: 0;
    width: 100%; height: 6.4%;
    min-height: 54px; border-top: 1px solid #c1c1c1;
    background-color: #f7f7f7;
    ul{
        display: flex;
        flex-direction: row;
        margin-top: 10px;
        li{
            flex: 1;
            text-align: center;
            transition: transform 300ms /* 500ms*/ linear 0s;
            &:hover{
                transform: scale(1.1);
            }
            a{  display: flex;
                flex-direction: column;
                color: #c0c1c2;               
                svg{
                    align-self: center;
                    display: block;
                    font-size: 22px;
                    margin-bottom: 3px;
                }
            }
            a.active{
                color: #513736;
            }
        }
    }
}